@import "syntax";

pre {
    border: 1px #ddd solid;
    border-radius: 0;
    background: transparent;
    line-height: 1.2;
    max-width: 600px;
    overflow-x: auto;
    padding: 0 1rem;
}

// add more spacing surrounding
// code blocks
.highlight pre {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.highlighter-rouge:not(code) {
    margin: 0 -1rem;
}

.hljs {
  margin: -1rem -1rem;
}

pre code {
    white-space: pre;
    word-wrap: normal;
}

// This is needed for the old code snippets
// from the blog so they stay within the boundary
// of the blog
p code {
    max-width: 600px;
    overflow-x: auto;
    display: inline-block;
}

code {
    font-family: monospace;
    color: #000;
    font-size: 0.9em;
}

html, body {
    overflow-x: hidden;
    font-family: sans-serif;
}

article p {
    line-height: 1.25;
}

body {
    background: #fff;
    color: #000;
    padding-bottom: 2rem;
}

iframe[src*="youtube"] {
    width: 100%;
    height: 400px;
    margin: 20px 0;
}

.video-js {
    width: 100% !important;
    height: 0px !important;
    padding-top: 62.5%;
}

video {
    max-width: 100%;
}

.asciicast iframe {
    margin: 0 auto !important;
    display: block !important;
}

img {
    max-width: 100%;
    margin: 0 auto;
    display: block;
}

a {
    color: blue;
    text-decoration: underline;
}

h1 {
    padding-top: 0;
    font-size: 16pt;
}

h1, h2, h3, h4, h5 {
    text-align: left;
}

h1 small {
    font-size: 14pt;
}

.footnotes::before {
    content: "References:";
}

.footnotes {
    font-size: 8pt;
    margin-top: 50px;
}

blockquote {
    padding-left: 10px;
    margin-left: 0;
    border-left: 4px #aaa solid;
    font-size: 11pt;
    color: #666;
}

.sidebar {
    padding-top: 20px;
}

.sidebar .fa {
  width: 18px;
  text-align: center;
}

.sidebar, .sidebar a {
    color: #444;
}

.sidebar a {
    text-decoration: underline;
}

.sidebar h3 {
    font-size: 14pt;
    margin-top: 40px;
}

.sidebar h4, h5 {
    margin: 5px 0;
}

.sidebar h4 {
    font-weight: bold;
}

.btn {
  display: inline-block;
  padding: .1rem .75rem;
  background: #e9ecef;
  border: #343a40 1px solid;
  font-size: 0.9rem;
  font-weight: 400;
  line-height: 1.5;
  cursor: pointer;
  color: #000;
  text-decoration: none;
  transition: color .15s ease-in-out,
    background-color .15s ease-in-out,
    border-color .15s ease-in-out,
    box-shadow .15s ease-in-out;

  &:hover {
    text-decoration: none;
    background: #f8f9fa;
    color: #000;
  }

  &.btn-primary {
    border: #001933 1px solid;
    background: #0069d9;
    color: #fff;

    &:hover {
      background: #0062cc;
      color: #fff;
    }
  }
}

em {
    font-style: italic;
}

.subtitle {
    text-align: center;
    margin-bottom: 1rem;
    margin-top: -10px;
    font-style: italic;
    font-size: 10pt;
}

hr {
    border-color: #999;
}

.post-stub h2 {
    font-size: 12pt;
    font-weight: bold;
}

.post-stub .date {
    color: #999;
    display: inline-block;
    min-width: 6rem;
}

.date {
    color: #999;
}

.date a {
    color: #999;
    text-decoration: underline;
}

.container-basic {
  max-width: 960px;
  margin: 0 auto;

  pre {
    max-width: calc(100% + 2rem);
  }
}

.container {
  max-width: 960px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 2fr 1fr;

  .page-title {
    grid-column-start: 1;
    grid-column-end: 3;
  }

  .content {
    grid-column-start: 1;
  }

  .sidebar {
    grid-column-start: 2;
    padding-left: 2rem;
  }

  @media (max-width: 768px) {
    display: block;

    .sidebar {
      padding-left: 0;
    }
  }
}

@media (min-width: 576px) {
  .visible-xs {
    display: none;
  }
}

.page-title {
  margin-bottom: 0;
}

.text-center {
  text-align: center;
}

.text-muted {
  color: #444;
}

.external-links {
  padding-top: 0.5rem;

  & > p {
    position: relative;
  }

  & > p:not(:last-child) {
    padding-bottom: 0.5rem;
  }

  .context {
    position: absolute;
    top: -0.9rem;
    left: 0;
    font-size: 0.8rem;
    color: black;
  }
}

.pull-right {
  float: right;
}

.pull-left {
  float: left;
}

.icon {
  display: inline-block;
  line-height: .75em;
  
  path {
    fill: currentColor;
  }

  width: 0.4em;
  vertical-align: -0.2em;
}

.avatar {
  max-width: 50%;
  border-radius: 5px;
}

/* Imported from bootstrap, for the donation fee calculator */
* {
  box-sizing: border-box;
}

.form-control {
  display: block;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
  transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
}

button, input, select, textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button, input, optgroup, select, textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

.panel {
  margin-bottom: 20px;
  background-color: #fff;
  border: 1px solid transparent;
  border-radius: 4px;
  box-shadow: 0 1px 1px rgba(0,0,0,.05);
}

.panel-default {
    border-color: #ddd;
}

.panel-default > .panel-heading {
  color: #333;
  background-color: #f5f5f5;
  border-color: #ddd;
}

.panel-heading {
  padding: 10px 15px;
  border-bottom: 1px solid transparent;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.panel-body {
  padding: 15px;
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: 700;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

.checkbox-inline, .radio-inline {
  position: relative;
  display: inline-block;
  padding-left: 20px;
  margin-bottom: 0;
  font-weight: 400;
  vertical-align: middle;
  cursor: pointer;
  margin-right: 1rem;
}

.checkbox input[type=checkbox],
.checkbox-inline input[type=checkbox],
.radio input[type=radio],
.radio-inline input[type=radio] {
  position: absolute;
  margin-top: 4px\9;
  margin-left: -20px;
}

input[type=checkbox],
input[type=radio] {
  margin: 4px 0 0;
  margin-top: 1px\9;
  line-height: normal;
  box-sizing: border-box;
  padding: 0;
}

.input-group {
  position: relative;
  display: table;
  border-collapse: separate;
}

.input-group-addon:first-child {
    border-right: 0;
}

.input-group-addon:last-child {
    border-left: 0;
}

.input-group .form-control:first-child,
.input-group-addon:first-child,
.input-group-btn:first-child>.btn,
.input-group-btn:first-child>.btn-group>.btn,
.input-group-btn:first-child>.dropdown-toggle,
.input-group-btn:last-child>.btn-group:not(:last-child)>.btn,
.input-group-btn:last-child>.btn:not(:last-child):not(.dropdown-toggle) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.input-group .form-control:last-child,
.input-group-addon:last-child,
.input-group-btn:first-child>.btn-group:not(:first-child)>.btn,
.input-group-btn:first-child>.btn:not(:first-child),
.input-group-btn:last-child>.btn,
.input-group-btn:last-child>.btn-group>.btn,
.input-group-btn:last-child>.dropdown-toggle {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.input-group .form-control:not(:first-child):not(:last-child),
.input-group-addon:not(:first-child):not(:last-child),
.input-group-btn:not(:first-child):not(:last-child) {
    border-radius: 0;
}

.input-group .form-control {
  position: relative;
  z-index: 2;
  float: left;
  width: 100%;
  margin-bottom: 0;
}

.input-group-addon {
  padding: 6px 12px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  color: #555;
  text-align: center;
  background-color: #eee;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.input-group-addon, .input-group-btn {
  width: 1%;
  white-space: nowrap;
  vertical-align: middle;
}

.input-group .form-control,
.input-group-addon, .input-group-btn {
    display: table-cell;
}

.table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 20px;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

th {
  text-align: left;
}

.table>caption+thead>tr:first-child>td,
.table>caption+thead>tr:first-child>th,
.table>colgroup+thead>tr:first-child>td,
.table>colgroup+thead>tr:first-child>th,
.table>thead:first-child>tr:first-child>td,
.table>thead:first-child>tr:first-child>th {
    border-top: 0;
}

.table>thead>tr>th {
  vertical-align: bottom;
  border-bottom: 2px solid #ddd;
}

.table>tbody>tr>td,
.table>tbody>tr>th,
.table>tfoot>tr>td,
.table>tfoot>tr>th,
.table>thead>tr>td,
.table>thead>tr>th {
  padding: 8px;
  line-height: 1.42857143;
  vertical-align: top;
  border-top: 1px solid #ddd;
}

.table>tbody>tr.active>td,
.table>tbody>tr.active>th,
.table>tbody>tr>td.active,
.table>tbody>tr>th.active,
.table>tfoot>tr.active>td,
.table>tfoot>tr.active>th,
.table>tfoot>tr>td.active,
.table>tfoot>tr>th.active,
.table>thead>tr.active>td,
.table>thead>tr.active>th,
.table>thead>tr>td.active,
.table>thead>tr>th.active {
    background-color: #f5f5f5;
}

details {
  background: #eee;
  padding: 0.5rem;

  summary {
    cursor: pointer;
  }

  p:last-child {
    margin-bottom: 0;
  }
}

abbrev {
  border-bottom: 1px dotted #000;
}